import React, { useState } from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons"
import { useSpring, animated } from "react-spring"
import { Link } from "gatsby"

import "./css/components.css"
import "semantic-ui-css/semantic.min.css"

import astrascapeLogo from "../../assets/astrascape-icon.png"

export default function SideNav() {
  const [panelOpen, setPanelOpen] = useState(false)
  const panelProps = useSpring({
    transform: panelOpen ? "translate3d(0%, 0, 0)" : "translate3d(100%, 0, 0)",
    delay: 100,
  })
  const buttonProps = useSpring({
    transform: panelOpen ? "translate3d(0%, 0, 0)" : "translate3d(250%, 0, 0)",
    delay: 500,
  })

  const maskProps = useSpring({
    opacity: panelOpen ? 0.8 : 0,
    transform: panelOpen ? "translate3d(0%, 0, 0)" : "translate3d(-100%, 0, 0)",
  })

  return (
    <>
      <FontAwesomeIcon icon={faBars} onClick={() => setPanelOpen(a => !a)} />
      <animated.div style={panelProps} className="panel">
        <animated.div style={buttonProps} className="panel-close-icon">
          <FontAwesomeIcon icon={faTimes} onClick={() => setPanelOpen(false)} />
        </animated.div>
        <div className="panel-links">
          <ul>
            <li>
              <Link to="/#about">About</Link>
            </li>
            <li>
              <Link to="/#services">Services</Link>
            </li>
            <li>
              <Link to="/#caseStudies">Case Studies</Link>
            </li>
            <li>
              <Link to="#">Contact</Link>
            </li>
            <li className="panel-about">
              <img
                style={{ filter: "invert(1)", marginTop: "100px" }}
                src={astrascapeLogo}
                alt="astrascape logo"
              />
            </li>
          </ul>
        </div>
      </animated.div>
      {/* for black mask  */}
      <animated.div
        className="panel-mask"
        style={maskProps}
        onClick={() => setPanelOpen(false)}
      ></animated.div>
    </>
  )
}
